import React from 'react';
import { MDBDataTableV5 } from 'mdbreact';
import { Badge, Button } from 'react-bootstrap';

export default function OfficeDocumentData() {

    const COLUMNS = [
        {
            label: 'STT',
            field: 'no',
            width: 100,
        },
        {
            label: 'Tn',
            field: 'name',
            width: 300,
        },
        {
            label: 'S vn bn',
            field: 'number',
            width: 100,
        },
        {
            label: 'Loi',
            field: 'type',
            width: 200,
        },
        {
            label: 'Ngy ban hnh',
            field: 'date',
            sort: 'asc',
            width: 100,
        },
        {
            label: 'Link',
            field: 'url',
            sort: 'disabled',
            width: 150,
        },
        {
            label: 'Trng thi',
            field: 'status',
            width: 100,
        },
    ]

    const ROWS = [
        {
            no: 1,
            name: 'K HOCH T chc chng trnh Thit k CV CV Contest nm 2022 Khoa Cng ngh Thng tin',
            number: 6,
            type: 'KH-HSV-CNTT',
            date: '2022/02/04',
            url: 'https://docs.google.com/document/d/1ge1iHfXM1c_wwTpMSSX49wDJzgSbkDJj/edit?usp=sharing&ouid=115258241827326023436&rtpof=true&sd=true',
            status: 'Active',
        },
        {
            no: 2,
            name: 'K HOCH T chc cuc thi hc thut Hackathon m rng nm 2022 Khoa Cng ngh Thng tin',
            number: 5,
            type: 'KH/TN-CNTT',
            date: '2022/02/22',
            url: 'https://docs.google.com/document/d/1lJXEWdb9yyQoEuYTVKG119NhYn55ryrt/edit?usp=sharing&ouid=115258241827326023436&rtpof=true&sd=true',
            status: 'Active',
        },
        {
            no: 3,
            name: 'K HOCH T chc Tri tp hun k nng v chng trnh giao lu Cn b on - Hi Khoa Cng ngh Thng Tin',
            number: 11,
            type: 'KH-HSV-CNTT',
            date: '2022/03/03',
            url: 'https://docs.google.com/document/d/1uN2gzIeoLmtaenM1EpPl9lEDQO5j17B5/edit?usp=sharing&ouid=115258241827326023436&rtpof=true&sd=true',
            status: 'Active',
        },
        {
            no: 4,
            name: 'K HOCH T chc chng trnh Noel m p nm 2021 Khoa Cng ngh Thng tin',
            number: 4,
            type: 'KHLT-TN-HSV',
            date: '2021/12/20',
            url: 'https://docs.google.com/document/d/18prwHNYCsg7VL4vxrD-MHKG7m00NQpOK/edit?usp=sharing&ouid=115258241827326023436&rtpof=true&sd=true',
            status: 'Active',
        },
        {
            no: 5,
            name: 'K HOCH T chc chng trnh  IT SNG TO nm 2021 K nim 39 nm ngy Nh gio Vit Nam (20/11/1982 - 20/11/2021) Khoa Cng ngh Thng tin',
            number: 3,
            type: 'KH-HSV-CNTT',
            date: '2021/10/28',
            url: 'https://docs.google.com/document/d/1HrEqcLhoB8oFYyCoPfw_CEFRUN72VMdq/edit?usp=sharing&ouid=115258241827326023436&rtpof=true&sd=true',
            status: 'Active',
        },
        {
            no: 6,
            name: 'K HOCH T chc chng trnh Th dc Online - Say bye Covid Khoa Cng ngh Thng tin nm 2021',
            number: 8,
            type: 'KHLT-TN-HSV-CNTT',
            date: '2021/08/05',
            url: 'https://docs.google.com/document/d/14YXNbenuuBiOJzJ9gSLPZVbzLAUnUL8i/edit?usp=sharing&ouid=115258241827326023436&rtpof=true&sd=true',
            status: 'Active',
        },
        {
            no: 7,
            name: 'K HOCH T chc chng trnh Trung thu yu thng nm 2021 Khoa Cng ngh Thng tin',
            number: 4,
            type: 'KHLT-TN-HSV',
            date: '2021/08/30',
            url: 'https://docs.google.com/document/d/1gXmeQN6_JaK8DdKhu43Aa1hi1_x97n6t/edit?usp=sharing&ouid=115258241827326023436&rtpof=true&sd=true',
            status: 'Active',
        },
    ]

    const exportData = (columns, rows) => {
        const rowsData = rows.map(data => {
            const getLink = (url) => {
                return (
                    <Button
                        size="sm" variant="outline-success"
                        href={url} target="_blank">
                        Xem
                    </Button>
                )
            }

            const getStatus = (status) => {
                return (
                    <Badge
                        pill
                        bg={status.toLowerCase() === 'active' ? 'primary' : 'danger'}
                    >
                        {status}
                    </Badge>
                )
            }
            return {
                ...data,
                url: getLink(data.url),
                status: getStatus(data.status),
            }
        })

        return {
            columns: columns,
            rows: rowsData
        }
    }

    return (
        <MDBDataTableV5
            hover fullPagination
            searchTop searchBottom={false}
            entriesOptions={[5, 10, 20, 50, 100]}
            entries={5}
            pagesAmount={4}
            data={exportData(COLUMNS, ROWS)} />
    );
}